
import { defineComponent, ref, watch } from 'vue'
import { useStore } from '@/store'

export default defineComponent({
  name: 'PageModal',
  props: {
    modalConfig: {
      type: Object,
      required: true
    },
    modalData: {
      type: Object,
      default: () => ({})
    },
    hiddenItem: {
      type: Array,
      default: () => []
    },
    pageName: {
      type: String,
      required: true
    },
    otherFormData: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const store = useStore()
    const dialogVisible = ref(false)
    const formData = ref<any>({})

    //监听传入的props的改变，源数据内存地址改变，所以这里要用getter形式，
    watch(
      () => props.modalData,
      (newVal) => {
        for (const item of props.modalConfig.formItems) {
          formData.value[item.field] = newVal[item.field]
        }
      }
    )

    const handleConfirmClick = () => {
      dialogVisible.value = false
      if (Object.keys(props.modalData).length) {
        //编辑时确认:
        store.dispatch('systemModule/editPageDataAction', {
          pageName: props.pageName,
          editData: { ...formData.value, ...props.otherFormData },
          id: props.modalData.id
        })
      } else {
        //新增时确认:
        store.dispatch('systemModule/addPageDataAction', {
          pageName: props.pageName,
          newData: { ...formData.value, ...props.otherFormData }
        })
      }
    }
    return {
      dialogVisible,
      formData,
      handleConfirmClick
    }
  }
})
