
import { defineComponent, PropType, watch, ref } from 'vue'
import { IFormItem } from './type'

export default defineComponent({
  name: 'GForm',
  props: {
    modelValue: {
      type: Object,
      required: true
    },
    formItems: {
      type: Array as PropType<IFormItem[]>,
      default: () => []
    },
    //类似响应式，决定一行显示几个表单
    rowLayout: {
      type: Object,
      default: () => ({
        xs: 24, // 屏幕 < 768px 一行显示一个表单
        sm: 24, // 768px < 屏幕 < 992px 一行显示一个表单
        md: 12, // 992px < 屏幕 < 1200px 一行显示两个表单
        lg: 8, // 1200px < 屏幕 < 1920px 一行显示三个表单
        xl: 6 // 屏幕 > 1920px 一行显示四个表单
      })
    },
    labelWidth: {
      type: String,
      default: '100px'
    },
    formItemStyle: {
      type: Object,
      default: () => ({ padding: '10px 5px;' })
    },
    hiddenItem: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    //拷贝一份，不直接引用props.modelVal,避免违反改变props的原则
    const formData: any = ref({ ...props.modelValue })
    //之后监听formData,发送新值出去
    watch(
      formData,
      (newVal) => {
        emit('update:modelValue', newVal)
      },
      {
        deep: true,
        immediate: true
      }
    )

    return {
      formData
    }
  }
})
